import QRCode from 'qrcodejs2';
import clientId_ from '@/components/global';
export default {
    name: "login",
    data(){
        return{
            toUrl: null,
            num: 0,
            classfiy: 0,
            user: "",   // 账号
            password: "",   // 密码
            pwdType: "password",
            Cid:0,
            qrocode:null
        }
    },
    created(){
        this.toUrl = window.localStorage.getItem("clientId");
        this.Cid = window.localStorage.getItem("Cid");
        if(!this.Cid || window.localStorage.getItem("Cid") == 'null'){
            this.Cid = 0;
        }
        this.qrocode = this.toUrl +'|'+this.Cid;
    },
    mounted(){
        this.qrcode();
    },
    methods:{
        // 账号密码登录
        login(){
            var that = this;
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.login,
                type: "post",
                data: {
                    account: this.user,
                    password: this.password,
                    getui_id:this.Cid,
                    is_watch:1
                },
                success: (response, allResponse) => {
                    window.localStorage.setItem("access_token", allResponse.data.access_token);
                    window.localStorage.setItem("user_id", allResponse.user_id);
                    window.localStorage.setItem("login_time", this.common.commonFun.getNowFormatDate());
                    if(!window.localStorage.getItem("pc_access_token")) {
                        that.$router.push({
                            path : '/home',
                        })
                    }
                }
            });
        },
        // 隐藏密码
        conceal(){
            this.pwdType = this.pwdType === 'password'?'text':'password';
        },
        // 显示密码
        show(){
            this.pwdType = this.pwdType === 'password'?'text':'password';
        },
        // 切换账号登录
        denglu(){
            this.classfiy = 1;
        },
        // 切换二维码登录
        denglus(){
            this.classfiy = 0;
            this.$router.go(0)
        },
        // 生成二维码
        qrcode(){
            let qrcode = new QRCode('qrcode',{
                width: 200, // 设置宽度，单位像素
                height: 200, // 设置高度，单位像素
                text: this.qrocode,   // 设置二维码内容或跳转地址
            })
            console.log(this.qrocode)
        }
    }
}



